.header {
  width: 100%;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header__logo-moyo-img {
  max-height: 50px;
  padding: 10px 20px;
  display: block;
}

.header__nav {
  margin: auto;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__list-item {
  margin-right: 30px;
}

.header__list-item:last-child {
  margin-right: 10px;
}

.header__link {
  color: #060b35;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 50px;
  padding: 0;
  line-height: 1;
  text-decoration: none;
  display: flex;
  position: relative;
}

.header__link:hover {
  color: #00acdc;
}

.header__link.is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 2px;
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
}

.body {
  margin: 0;
  padding: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #000;
  width: 200px;
  padding: 5px;
  transition: transform .3s, box-shadow .3s;
  position: relative;
}

.card:hover {
  transform: scale(1.2);
  box-shadow: 0 4px 10px #0003;
}

.card__img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: cover;
  width: 100%;
  height: 150px;
}

.card__title {
  text-align: center;
  color: #007aff;
  justify-content: center;
  width: 166px;
  height: 36px;
  padding: 15px;
  transition: color .3s;
  display: flex;
}

.card__code {
  color: #616070;
  width: 98px;
  padding-inline: 25px;
  display: inline;
}

.card__review {
  color: #060b35;
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__button {
  text-align: center;
  justify-content: center;
  display: flex;
}

.card__buy {
  color: #fff;
  background-color: #007aff;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: flex;
}

.card__buy:hover {
  color: #fff;
  cursor: pointer;
  background-color: green;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__stars {
  justify-content: space-between;
  display: flex;
}

.card__star {
  background-repeat: no-repeat;
  background-size: cover;
  width: 12px;
  height: 12px;
}

.card__star--active {
  background-image: url("star-active.edda4c43.svg");
}

.card__star--inactive {
  background-image: url("star.822d561e.svg");
}

.main {
  grid-template-columns: 1fr;
  justify-content: center;
  place-items: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 1fr);
  }
}
